import React from 'react'
import { Link, graphql, useStaticQuery } from 'gatsby'
import Layout from '../components/layout'
import * as blogStyles from './blog.module.scss'
/*import { Img } from 'gatsby'*/

import { GatsbyImage , getImage } from "gatsby-plugin-image"

const BlogPage = () => {

  /*
  const data = useStaticQuery(
    graphql`
    query  {
      allContentfulBlogPost(
        sort:{
          fields:publishedDate
          order: DESC
        }
      ){
        edges{
          node{
            title
            slug
            publishedDate(formatString:"MMM Do, YYYY")
          }
        }
      }
    }`
  )
  */
 const data = useStaticQuery (
   graphql`query  {
    allMediumPost {
      edges {
        node {
          id
          uniqueSlug
          title
          previewContent {
            subtitle
          }
          firstPublishedAt(fromNow: true)
          publishfmt:firstPublishedAt(formatString:"YYYYMMDD")
          previewContent2 {
            isFullContent
            bodyModel {
              paragraphs {
                metadata {
                  id
                  unsplashPhotoId
                  originalWidth
                  isFeatured
                }
              }
            }
          }
          author {
            username
          }
        }
      }
    }
  }`
 )
  

  //console.log(data);
  return (
    <div>
      <Layout>
        <h1>Blog</h1>
        <ol className={blogStyles.posts}>

          {data.allMediumPost.edges
          .sort((a,b)=>b.node.publishfmt - a.node.publishfmt)
          .map((edge) => {
            const irl = edge.node.previewContent2.bodyModel.paragraphs.filter( (a) => a.metadata?.isFeatured)[0]?.metadata.id
            console.log(irl)
            //console.log('Image',getImage(`https://miro.medium.com/${irl}`) )
            return (
              <li className={blogStyles.post}>
                <a href={ `https://www.medium.com/@${edge.node.author.username}/${edge.node.uniqueSlug}`} target= "_blank">
                  
                  <img src={ `https://miro.medium.com/${irl}`} alt={edge.node.title}/>
                  <h2>{edge.node.title}</h2>
                  <p>{edge.node.previewContent.subtitle}</p>
                  <p>{edge.node.firstPublishedAt}</p>
                  
                </a>
              </li>
            )
          })}

        </ol>
      </Layout>
    </div>
  )
}

export default BlogPage